@font-face {
	font-family: "dungeon-master";
	src: url("../../Assets/Fonts/dungeon-master.ttf?sz4w0x") format("truetype"),
		url("../../Assets/Fonts/dungeon-master.woff?sz4w0x") format("woff"),
		url("../../Assets/Fonts/dungeon-master.svg?sz4w0x#dungeon-master")
			format("svg");
	font-weight: normal;
	font-style: normal;
	font-display: block;
}

i {
	/* use !important to prevent issues with browser extensions that change fonts */
	font-family: "dungeon-master" !important;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;
	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	&.info:before {
		content: "\e900";
	}
	&.dnd:before {
		content: "\e901";
	}
	&.d20:before {
		content: "\e902";
	}
	&.edit:before {
		content: "\e903";
	}
	&.skull:before {
		content: "\e904";
	}
	&.damage:before {
		content: "\e905";
	}
	&.heart:before {
		content: "\e906";
	}
}
